body,html, #root{
  margin: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  background-color:#6ac9b4;
}

.movement-instructions {
  position: absolute;
  top: 20px;
  left: 20px;
  z-index: 999;
  background-color: rgba(255, 255, 255, 0.2);
  padding: 10px;
  border-radius: 5px;
  font-size: 16px;
  font-family: cursive, sans-serif;
}
